  /**
   * éè¯¯æ¶æ¯
   */
  message: string
  /**
   * éè¯¯ä»£ç 
   */
  code?: string
  /**
   * éè¯¯å æ 
   */
  stack?: string
  /**
   * éè¯¯ä¸ä¸æ
   */
  context?: Record<string, unknown>
  /**
   * éè¯¯è¯¦æ
   */
  details?: Record<string, unknown>
  /**
   * éè¯¯æ¥æº
   */
  source?: string
  /**
   * éè¯¯åççæä»¶
   */
  file?: string
  /**
   * éè¯¯åççè¡å·
   */
  line?: number
  /**
   * æ¶é´æ³
   */
  timestamp: number
  /**
   * éè¯¯ID
   */
  id: string
  /**
   * ç¸å³äºä»¶
   */
