  /**
   * é»è®¤é»åå
   */
  blacklist: [],
  /**
   * é»è®¤ç½åå
   */
  whitelist: undefined,
  /**
   * é»è®¤æ¹éåéå¤§å°
   */
  batchSize: 20,
  /**
   * é»è®¤æ¹éåéé´éï¼æ¯«ç§ï¼
   */
