/**
 * æµè§å¨å·¥å·å½æ°
 */
export const browserUtils = {
  /**
   * æ£æµæµè§å¨ç±»å
   * @returns {Object} æµè§å¨ä¿¡æ¯
   * @returns {string} name - æµè§å¨åç§°
   * @returns {string} version - æµè§å¨çæ¬
   * @returns {string} platform - å¹³å°
   */
  getBrowser: (): {
    name: string;
    version: string;
    platform: string;
  } => {
    if (!isBrowser() || typeof navigator === "undefined") {
      return {
        name: "unknown",
        version: "0",
        platform: "unknown",
      };
    }

    const result = detectBrowser(navigator.userAgent);
    return {
      name: result.name,
      version: result.version,
      platform: navigator.platform,
    };
  },

  /**
   * æ£æµè®¾å¤ç±»å
   * @returns {('mobile'|'tablet'|'desktop'|'unknown')} è®¾å¤ç±»å
   */
  getDeviceType: (): "mobile" | "tablet" | "desktop" | "unknown" => {
    if (!isBrowser() || typeof navigator === "undefined") {
      return "unknown";
    }

    const result = detectDevice(navigator.userAgent);
    return result.type;
  },

  /**
   * æ£æµç½ç»ç¶æ
   * @returns {Object} ç½ç»ç¶æ
   * @returns {('online'|'offline')} type - ç½ç»ç±»å
   * @returns {('2g'|'3g'|'4g'|'5g'|'unknown')} effectiveType - ææç½ç»ç±»å
   * @returns {number} rtt - å¾è¿æ¶é´
   * @returns {number} downlink - ä¸è¡éåº¦
   */
  getNetworkState: (): {
    type: "online" | "offline";
    effectiveType: "2g" | "3g" | "4g" | "5g" | "unknown";
    rtt: number;
    downlink: number;
  } => {
    if (!isBrowser() || typeof navigator === "undefined") {
      return {
        type: "offline",
        effectiveType: "unknown",
        rtt: 0,
        downlink: 0,
      };
    }

    const type = navigator.onLine ? "online" : "offline";
    let effectiveType: "2g" | "3g" | "4g" | "5g" | "unknown" = "unknown";
    let rtt = 0;
    let downlink = 0;

    if ("connection" in navigator) {
      const connection = (
        navigator as unknown as { connection?: NetworkConnection }
      ).connection;
      effectiveType =
        (connection?.effectiveType as "2g" | "3g" | "4g" | "5g" | "unknown") ||
        "unknown";
      rtt = connection?.rtt || 0;
      downlink = connection?.downlink || 0;
    }

    return {
      type,
      effectiveType,
      rtt,
      downlink,
    };
  },
};

/**
 * å­å¨å·¥å·å½æ°
 */
export const storageUtils = {
  /**
   * å®å¨è·åæ¬å°å­å¨
   * @param {string} key - å­å¨é®
   * @param {Function} [parser] - è§£æå½æ°
   * @returns {unknown} å­å¨å¼
   */
  get: (key: string, parser?: (value: string) => unknown): unknown => {
    if (!isBrowser() || typeof localStorage === "undefined") {
      return null;
    }
    try {
      const value = localStorage.getItem(key);
      if (value === null) return null;
      return parser ? parser(value) : value;
    } catch {
      return null;
    }
  },

  /**
   * å®å¨è®¾ç½®æ¬å°å­å¨
   * @param {string} key - å­å¨é®
   * @param {unknown} value - å­å¨å¼
   * @returns {boolean} æ¯å¦è®¾ç½®æå
   */
  set: (key: string, value: unknown): boolean => {
    if (!isBrowser() || typeof localStorage === "undefined") {
      return false;
    }
    try {
      const stringValue =
        typeof value === "string" ? value : JSON.stringify(value);
      localStorage.setItem(key, stringValue);
      return true;
    } catch {
      return false;
    }
  },

  /**
   * å®å¨ç§»é¤æ¬å°å­å¨
   * @param {string} key - å­å¨é®
   * @returns {boolean} æ¯å¦ç§»é¤æå
   */
  remove: (key: string): boolean => {
    if (!isBrowser() || typeof localStorage === "undefined") {
      return false;
    }
    try {
      localStorage.removeItem(key);
      return true;
    } catch {
      return false;
    }
  },

  /**
   * å®å¨æ¸ç©ºæ¬å°å­å¨
   * @returns {boolean} æ¯å¦æ¸ç©ºæå
   */
  clear: (): boolean => {
    if (!isBrowser() || typeof localStorage === "undefined") {
      return false;
    }
    try {
      localStorage.clear();
      return true;
    } catch {
      return false;
    }
  },
};

/**
 * è·åæµè§å¨æ°æ®
 * @returns {BrowserData} æµè§å¨æ°æ®
 */
export function getBrowserData(): BrowserData {
  if (!isBrowser()) {
    return {
      device_id: getDeviceId(),
      event: "pageview",
      user_agent: "non-browser",
      device_width: 0,
      device_height: 0,
      is_online: true,
      connection_type: "unknown",
      app_code_name: "",
      app_name: "",
      language: "",
      platform: "",
      time_zone: Intl.DateTimeFormat().resolvedOptions().timeZone,
      browser_version: "",
      browser_name: "non-browser",
      browser_major_version: "0",
      engine_name: "unknown",
      engine_version: "0",
      device_pixel_ratio: 1,
      is_mobile: false,
      is_tablet: false,
      is_desktop: true,
      current_url: "",
      pathname: "",
      hostname: "",
      protocol: "",
      port: "",
      search: "",
      hash: "",
      document_url: "",
      referrer_url: "",
      content_type: "",
      document_title: "",
      document_charset: "",
      document_ready_state: "loading",
      screen_width: 0,
      screen_height: 0,
      screen_available_width: 0,
      screen_available_height: 0,
      screen_color_depth: 0,
      scroll_x: 0,
      scroll_y: 0,
      begin_time: Date.now(),
    };
  }

  try {
    const basicBrowserInfo = getBasicBrowserInfo();
    const detailedBrowserInfo = getDetailedBrowserInfo(
      basicBrowserInfo.user_agent,
    );
    const deviceInfo = getDeviceInfo(basicBrowserInfo.user_agent);
    const networkInfo = getNetworkInfo();
    const screenInfo = getScreenInfo();
    const pageInfo = getPageInfo();
    const scrollInfo = getScrollInfo();

    return {
      device_id: getDeviceId(),
      event: "pageview",
      ...basicBrowserInfo,
      ...deviceInfo,
      ...networkInfo,
      ...detailedBrowserInfo,
      ...screenInfo,
      ...pageInfo,
      ...scrollInfo,
      time_zone: Intl.DateTimeFormat().resolvedOptions().timeZone,
      begin_time: Date.now(),
    };
  } catch (error) {
    // å¦æè·åæäºä¿¡æ¯å¤±è´¥ï¼è¿ååºæ¬æ°æ®
    return {
      device_id: getDeviceId(),
      event: "pageview",
      user_agent: navigator?.userAgent || "unknown",
      device_width: window?.innerWidth || 0,
      device_height: window?.innerHeight || 0,
      is_online: navigator?.onLine || false,
      connection_type:
        (navigator as unknown as { connection?: NetworkConnection })?.connection
          ?.type || "unknown",
      downlink: (navigator as unknown as { connection?: NetworkConnection })
        ?.connection?.downlink,
      effective_type: (
        navigator as unknown as { connection?: NetworkConnection }
      )?.connection?.effectiveType,
      rtt: (navigator as unknown as { connection?: NetworkConnection })
        ?.connection?.rtt,
      app_code_name: navigator?.appCodeName || "",
      app_name: navigator?.appName || "",
      language: navigator?.language || "",
      platform: navigator?.platform || "",
      time_zone: Intl.DateTimeFormat().resolvedOptions().timeZone,
      browser_version: navigator?.appVersion,
      device_pixel_ratio: window?.devicePixelRatio || 1,
      is_mobile: false,
      is_tablet: false,
      is_desktop: true,
      current_url: window?.location?.href || "",
      pathname: window?.location?.pathname || "",
      hostname: window?.location?.hostname || "",
      protocol: window?.location?.protocol || "",
      port: window?.location?.port || "",
      search: window?.location?.search || "",
      hash: window?.location?.hash || "",
      document_url: document?.URL || "",
      referrer_url: document?.referrer || "",
      content_type: document?.contentType || "",
      document_title: document?.title || "",
      document_charset: document?.characterSet || document?.charset || "",
      document_ready_state: document?.readyState || "loading",
      screen_width: screen?.width || 0,
      screen_height: screen?.height || 0,
      screen_available_width: screen?.availWidth || 0,
      screen_available_height: screen?.availHeight || 0,
      screen_color_depth: screen?.colorDepth || 0,
      scroll_x:
        window?.pageXOffset || document?.documentElement?.scrollLeft || 0,
      scroll_y:
        window?.pageYOffset || document?.documentElement?.scrollTop || 0,
      begin_time: Date.now(),
    };
  }
}

/**
 * æµè§å¨æä»¶
 */
export const browserPlugin: IPlugin = {
  name: "browser",
  version: "1.0.0",
  description:
    "Browser information plugin for collecting browser data and device information",
  priority: 30,

  /**
   * äºä»¶è·è¸ªååè°
   */
  onTrack<T extends EventProperties>(payload: Payload<T>): Payload<T> {
    // æ·»å æµè§å¨æ°æ®å°äºä»¶å±æ§
    try {
      const browserData = getBrowserData();
      // åªæ·»å é¨åå³é®çæµè§å¨ä¿¡æ¯ï¼é¿åäºä»¶è¿å¤§
      const browserContext = {
        browser_name: browserData.browser_name,
        browser_version: browserData.browser_version,
        device_type: browserData.is_mobile
          ? "mobile"
          : browserData.is_tablet
            ? "tablet"
            : "desktop",
        platform: browserData.platform,
        user_agent: browserData.user_agent,
        screen_width: browserData.screen_width,
        screen_height: browserData.screen_height,
        is_online: browserData.is_online,
        connection_type: browserData.connection_type,
        current_url: browserData.current_url,
        referrer_url: browserData.referrer_url,
      };

      return {
        ...payload,
        properties: {
          ...payload.properties,
          ...browserContext,
        } as unknown as T,
      };
    } catch (error) {
      // å¦æè·åæµè§å¨æ°æ®å¤±è´¥ï¼è¿ååå§payload
      return payload;
    }
  },

  /**
   * è·åæä»¶ä¿¡æ¯
   */
  getInfo(): Record<string, any> {
    return {
      name: this.name,
      version: this.version,
      description: this.description,
      browserData: getBrowserData(),
      browserUtils: browserUtils.getBrowser(),
      deviceType: browserUtils.getDeviceType(),
      networkState: browserUtils.getNetworkState(),
    };
  },
};
